<section
  id="accueil"
  className="relative h-[90vh] min-h-[600px] flex items-center justify-start text-white px-8 md:px-20"
>
  {/* Images de fond */}
  {backgroundImages.map((image, index) => (
    <div
      key={index}
      className={`absolute inset-0 bg-cover bg-center transition-all duration-[1800ms] ease-in-out ${
        currentImageIndex === index ? "opacity-100 scale-100" : "opacity-0 scale-105"
      }`}
      style={{ backgroundImage: `url('${image}')` }}
    />
  ))}

  {/* Overlay clair */}
  <div className="absolute inset-0 bg-gradient-to-b from-white/10 via-white/5 to-white/10" />

  {/* Contenu principal aligné à gauche */}
  <div
    className={`relative z-20 w-full max-w-3xl transition-all duration-700 ${
      visible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-6"
    }`}
  >
    <div className="bg-white/10 backdrop-blur-md rounded-3xl p-6 md:p-10 border border-white/30 shadow-2xl pb-16 max-w-lg">
      <h1 className="text-3xl md:text-5xl lg:text-6xl font-extrabold mb-4 leading-tight tracking-tight text-white text-left drop-shadow-[0_0_10px_rgba(0,0,0,0.4)]">
        Construisons ensemble
        <br />
        <span className="text-white drop-shadow-[0_0_20px_rgba(255,255,255,0.5)]">
          l’avenir de vos projets
        </span>
      </h1>

      <p className="text-sm md:text-base mb-8 text-white/90 leading-relaxed text-left drop-shadow-[0_0_8px_rgba(0,0,0,0.3)]">
        Expert en construction métallique, installations solaires et infrastructures.
        <br className="hidden md:block" />
        Nous transformons vos idées en réalité avec qualité et innovation.
      </p>

      <div className="flex items-center gap-4">
        <UiButton
          size="lg"
          variant="outline"
          className="border-white text-white hover:bg-white hover:text-black bg-transparent font-semibold px-6 py-3 rounded-lg transition-transform duration-300 hover:scale-105"
          onClick={() =>
            document.getElementById("services")?.scrollIntoView({ behavior: "smooth" })
          }
        >
          Découvrez nos services
        </UiButton>
      </div>
    </div>
  </div>

  {/* Dots centrés en bas */}
  <div className="absolute left-1/2 -translate-x-1/2 z-30 bottom-12 md:bottom-16 flex gap-3">
    {backgroundImages.map((_, index) => (
      <button
        key={index}
        onClick={() => setCurrentImageIndex(index)}
        className={`h-3 w-3 md:h-4 md:w-4 rounded-full transition-all duration-300 focus:outline-none ${
          currentImageIndex === index
            ? "bg-white scale-125 shadow-[0_0_14px_rgba(255,255,255,0.85)]"
            : "bg-white/40 hover:bg-white/70"
        }`}
        aria-label={`Image ${index + 1}`}
      />
    ))}
  </div>
</section>
